export default {
	data() {
		return {
			micTabStatus: 0,
			scrollLeft: 0,
			micTab: [{
					name: "全部订单"
				},
				{
					name: "待发货"
				},
				{
					name: "待收货"
				},

			],
			shopList: [{
					"name": "周杰伦",
					"pic": 20,
					"micName": "黑色毛衣",
					"num": 1,
					"id": "1",
					isChecked: false,
				},
				{
					"name": "华晨宇",
					"pic": 10,
					"micName": "烟火里的尘埃",
					"num": 2,
					"id": "2",
					isChecked: false,
				}
			],
			tboxList: []
		}
	},
	onLoad(options) {
		this.micTabStatus = options.micTabStatus
		// 获取购物车数据
		this.tboxList = uni.getStorageSync("tbox_audio");
	},
	methods: {
		// 音乐tab
		micTabClick(idx) {
			this.micTabStatus = idx;
			this.scrollLeft = (idx - 2) * 90
		},
		go_order() {
			uni.navigateTo({
				url: "/pages/orderDetails/orderDetails?orderType=" + 1
			})
		}
	}
}
